// работа с бургером
let burgerBtn = document.querySelector('.header__burgerBtn');
let burgerContainer = document.querySelector('.burgerContainer');
let burgerLinks = document.querySelectorAll('.burgerContainer a');

function openCloseBurger() {
  burgerBtn.classList.toggle( 'active' );
  burgerContainer.classList.toggle('active');
}

if ( burgerBtn !== null && burgerContainer !== null ) {
  burgerBtn.addEventListener( 'click', function() {
    openCloseBurger();
  } );

  burgerContainer.addEventListener( 'click', function(e) {
    if ( e.target === burgerContainer ) {
      openCloseBurger();
    }
  } );

  if ( burgerLinks.length > 0 ) {
    for ( let i=0; i < burgerLinks.length; i++ ) {
      burgerLinks[i].addEventListener( 'click', function() {
        openCloseBurger();
      } );
    }
  }
}
//**************************************************************************


//прокрутка страницы до нужного блока при выборе пункта меню
const curUrl = '';
const anchors = document.querySelectorAll('a[href*="'+curUrl+'#anchor"]')

if ( anchors.length > 0 ) {

  for (let i = 0; i < anchors.length; i++) {

    let anchor = anchors[i];
    anchor.addEventListener('click', function (e) {
      e.preventDefault()

      const href = anchor.getAttribute('href');
      const blockID = href.substring(href.indexOf('#') + 1);
      document.getElementById(blockID).scrollIntoView({
        behavior: 'smooth',
        block: 'start'
      })
    })
  }
}
