import "/assets/src/js/common";

let selectLists = document.querySelectorAll('.search__itemList select');

if ( selectLists.length > 0 ) {
  for ( let i = 0; i < selectLists.length; i++ ) {
    selectLists[i].addEventListener( 'focus', function () {
      this.parentElement.classList.add( 'active' );
    } );

    selectLists[i].addEventListener( 'blur', function () {
      this.parentElement.classList.remove( 'active' );
    } );
  }
}